.container {
  display: flex; /* フレックスボックスレイアウトを使用 */
  justify-content: center; /* 水平方向の中央揃え */
  align-items: center; /* 垂直方向の中央揃え */
  height: 100vh; /* 画面の高さ100%を占める */
  background-color: #f5f5f5; /* 背景色を薄いグレーに設定 */
}

.text-display {
  writing-mode: vertical-rl; /* 縦書き表示を有効にする（右から左へ） */
  text-orientation: upright; /* 文字を正立させる（90度回転させない） */
  font-size: 2rem; /* フォントサイズを大きく設定 */
  line-height: 2.5; /* 行間を広く設定（読みやすくするため） */
  color: #333; /* 文字色を濃いグレーに設定 */
  max-height: 80vh; /* 最大高さを画面の80%に制限 */
  overflow: hidden; /* 溢れたテキストを非表示にする */
  text-align: center; /* テキストを中央揃え */
  cursor: pointer; /* マウスカーソルをポインターに変更（クリック可能を示す） */
  user-select: none; /* テキストの選択を無効にする（クリック時のテキスト選択を防ぐ） */
}

.text-segment {
  display: inline-block; /* インラインブロック要素として表示 */
  margin: 0 0.5em; /* 左右に少し余白を追加 */
  opacity: 0; /* 初期状態では透明（非表示） */
  transition: opacity 0.3s ease-in-out; /* 透明度の変化にアニメーション効果を追加（0.3秒でスムーズに変化） */
}

.text-segment.visible {
  opacity: 1; /* 完全に不透明（表示状態） */
}

.text-segment.hidden {
  opacity: 0; /* 完全に透明（非表示状態） */
}

.start-screen {
  display: flex; /* フレックスボックスレイアウトを使用 */
  flex-direction: column; /* 縦方向に要素を並べる */
  justify-content: center; /* 水平方向の中央揃え */
  align-items: center; /* 垂直方向の中央揃え */
  height: 100vh; /* 画面の高さ100%を占める */
  background-color: #f5f5f5; /* 背景色を薄いグレーに設定 */
}

.start-title {
  font-size: 3rem; /* フォントサイズを大きく設定 */
  color: #333; /* 文字色を濃いグレーに設定 */
  margin-bottom: 2rem; /* 下に2remの余白を追加 */
  text-align: center; /* テキストを中央揃え */
}

.start-button {
  background-color: #4CAF50; /* 背景色を緑色に設定 */
  border: none; /* ボーダーを削除 */
  color: white; /* 文字色を白に設定 */
  padding: 15px 32px; /* 上下に15px、左右に32pxの余白を追加 */
  text-align: center; /* テキストを中央揃え */
  text-decoration: none; /* テキストの装飾を削除 */
  display: inline-block; /* インラインブロック要素として表示 */
  font-size: 1.5rem; /* フォントサイズを大きく設定 */
  margin: 4px 2px; /* 上下に4px、左右に2pxの余白を追加 */
  cursor: pointer; /* マウスカーソルをポインターに変更（クリック可能を示す） */
  border-radius: 8px; /* 角を丸くする */
  transition: background-color 0.3s ease; /* 背景色の変化にアニメーション効果を追加（0.3秒でスムーズに変化） */
}

.start-button:hover {
  background-color: #45a049; /* 背景色をやや濃い緑色に変更 */
}

.start-button:active {
  background-color: #3d8b40; /* 背景色をやや濃い緑色に変更 */
}
